                </div>\r\n                                <div class=\"card\">\r\n                                    <div class=\"card-body d-flex flex-column\">\r\n                                        <label>Номер торгов</label>\r\n                                        <p class=\"card-text text-wrap\">{{ item.tradeNumber + ', лот № ' + item.lotNumber}}</p>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"card\">\r\n                                    <div class=\"card-body d-flex flex-column\">\r\n                                        <label>Тип торгов</label>\r\n                                        <p class=\"card-text text-wrap\">{{ $t('trades.type.' + item.tradeType) }}</p>\r\n                                    </div>\r\n                                </div>\r\n\r\n                                <div class=\"card\">\r\n                                    <div class=\"card-body d-flex flex-column\">\r\n                                        <label>Статус торгов</label>\r\n                                        <p class=\"card-text text-wrap\">{{item.status}}</p>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"card\">\r\n                                    <div class=\"card-body d-flex flex-column\">\r\n                                        <label>Дата публикации</label>\r\n                                        <p class=\"card-text text-wrap\">{{item.publishDate}}</p>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"card\" v-if=\"item.eventTimeStart || item.eventTimeEnd\">\r\n                                    <div class=\"card-body d-flex flex-column\">\r\n                                        <label>Даты начала и окончания торгов</label>\r\n                                        <p class=\"card-text text-wrap\">{{item.eventTimeStart ? item.eventTimeStart : 'Не указано' +\r\n                                        ' - ' + item.eventTimeEnd ? item.eventTimeEnd : 'Не указано'}}</p>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"card\" v-if=\"item.eventTimeResult\">\r\n                                    <div class=\"card-body d-flex flex-column\">\r\n                                        <label>Дата окончания торгов</label>\r\n                                        <p class=\"card-text text-wrap\">{{item.eventTimeResult}}</p>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"card\" v-if=\"item.applicationTimeStart || item.applicationTimeEnd\">\r\n                                    <div class=\"card-body d-flex flex-column\">\r\n                                        <label>Даты начала и окончания приема заявок</label>\r\n                                        <p class=\"card-text text-wrap\">{{(item.applicationTimeStart ? item.applicationTimeStart : 'Не указано') +\r\n                                        ' - ' + (item.applicationTimeEnd ? item.applicationTimeEnd : 'Не указано')}}</p>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"card\">\r\n                                    <div class=\"card-body d-flex flex-column\">\r\n                                        <label>Описание</label>\r\n                                        <p class=\"card-text text-wrap\">{{item.description}}</p>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"card\">\r\n                                    <div class=\"card-body d-flex flex-column\">\r\n                                        <label>Начальная цена</label>\r\n                                        <p class=\"card-text text-wrap\">{{item.startPrice | priceFormat}} ₽</p>\r\n                                    </div>\r\n                                </div>\r\n\r\n                                <div class=\"card\" v-if=\"item.currentPrice\">\r\n                                    <div class=\"card-body d-flex flex-column\">\r\n                                        <label>Текущая цена</label>\r\n                                        <p class=\"card-text text-wrap\">{{item.currentPrice | priceFormat}} ₽</p>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"card\" v-if=\"item.minPrice\">\r\n                                    <div class=\"card-body d-flex flex-column\">\r\n                                        <label>Минимальная цена</label>\r\n                                        <p class=\"card-text text-wrap\">{{item.minPrice | priceFormat}} ₽</p>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"card\" v-if=\"item.stepPrice && (item.tradeType === 'CloseAuction' || item.tradeType === 'OpenAuction')\">\r\n                                    <div class=\"card-body d-flex flex-column\">\r\n                                        <label>Шаг аукциона</label>\r\n                                        <p class=\"card-text text-wrap\">{{item.stepPrice.value + (item.stepPrice.type == 'rubles' ? '₽' : '%')}}</p>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"card\" v-if=\"item.deposit && (item.tradeType === 'CloseAuction' || item.tradeType === 'OpenAuction')\">\r\n                                    <div class=\"card-body d-flex flex-column\">\r\n                                        <label>Задаток</label>\r\n                                        <p class=\"card-text text-wrap\">{{item.deposit.value + (item.deposit.type == 'rubles' ? '₽' : '%')}}</p>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"card\" v-if=\"item.priceReduction && (item.tradeType === 'PublicOffer' || item.tradeType === 'ClosePublicOffer')\">\r\n                                    <div class=\"card-body d-flex flex-column\">\r\n                                        <label>Информация о снижении цены</label>\r\n                                        <p class=\"card-text text-wrap\" v-html=\"item.priceReduction\"></p>\r\n                                    </div>\r\n                                </div>\r\n\r\n                                <div class=\"card\" v-if=\"item.photos.length > 0\">\r\n                                    <div class=\"card-body d-flex flex-column\">\r\n                                        <label>Фотографии лота</label>\r\n                                        <p class=\"card-text text-wrap\" v-for=\"(photo, index) in item.photos\">\r\n                                            <a :href=\"photo.main\" target=\"_blank\">{{'Посмотреть фото №' + (index+1)}}</a>\r\n                                        </p>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"card\">\r\n                                    <div class=\"card-body d-flex flex-column\">\r\n                                        <label>Категории</label>\r\n                                        <ul>\r\n                                            <li v-for=\"category in item.categories\">\r\n                                                {{category.label}}\r\n                                                <ul v-if=\"category.subcategories.length > 0\">\r\n                                                    <li v-for=\"subcategory in category.subcategories\">{{subcategory.label}}</li>\r\n                                                </ul>\r\n                                            </li>\r\n                                        </ul>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"card\">\r\n                                    <div class=\"card-body d-flex flex-column\">\r\n                                        <label>Регионы</label>\r\n                                        <ul>\r\n                                            <li v-for=\"region in item.location\">\r\n                                                {{$t('regions.' + region.code) + (region.isDebtorRegion ? '- регион должника' : '')}}\r\n                                            </li>\r\n                                        </ul>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"card\">\r\n                                    <div class=\"card-body d-flex flex-column\">\r\n                                        <label>Ссылка на лот</label>\r\n                                        <a :href=\"item.link\" target=\"_blank\">Перейти</a>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"card\">\r\n                                    <div class=\"card-body d-flex flex-column\">\r\n                                        <label>Ссылка на лот на федресурсе</label>\r\n                                        <a :href=\"item.efrsbLink\" target=\"_blank\">Перейти</a>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"card\" v-if=\"item.descriptionExtracts.length>0\">\r\n                                    <div class=\"card-body d-flex flex-column\">\r\n                                        <label>Данные из описания</label>\r\n                                        <ul>\r\n                                            <li v-for=\"extract in item.descriptionExtracts\">\r\n                                                {{extract.tradeSubject == null ? 'Другое: ': extract.tradeSubject+ ':'}}\r\n                                                <ul v-if=\"extract.extracts.length > 0\">\r\n                                                    <li v-for=\"property in extract.extracts\" v-if=\"property.value\">{{property.title + ' - ' + property.value}}</li>\r\n                                                </ul>\r\n                                            </li>\r\n                                        </ul>\r\n                                    </div>\r\n                                </div>\r\n\r\n\r\n                            </div>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </section>\r\n    </fragment>\r\n</template>\r\n\r\n<script>\r\nimport {mapActions, mapGetters} from \"vuex\";\r\n\r\nexport default {\r\n    name: \"ShowLot\",\r\n    computed: {\r\n        ...mapGetters(['item']),\r\n    },\r\n    methods: {\r\n        ...mapActions(['editItem']),\r\n    },\r\n    async created() {\r\n        this.$store.commit('setCurrentRoute', this.$route.path.replace(/(\\/*$)/, \"\"))\r\n        await this.editItem('show')\r\n    }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n    a {\r\n        color: var(--bs-link-color);\r\n        text-decoration: underline;\r\n    }\r\n    ul {\r\n        margin-bottom: 0;\r\n    }\r\n</style>\r\n"],"sourceRoot":""}]);
